
<template>
  <!--
    /**使用说明
    /*1、在父组件中导入组件:<flip @customEvent='customEvent' :Body='patientBody' :Condition='condition'></flip>
    /*2、customEvent事件，将此函数放到父组件的methods中customEvent(Condition){}  
    /*3、Body是我们请求回来数据的body
    /*4、Condition是我们要给后台的请求对象       
  -->
  <div class='flip'>
    <div class='flipBtn'>
      <div class="btn-group pull-right" role="group" style='margin-left:10px;' aria-label="...">
        <button type="button" class="btn btn-default" @click='homePage'>首页</button>
        <button type="button" class="btn btn-default" @click='previousPage'>上一页</button>
        <button type="button" class="btn btn-default">{{Body.pages==0?0:Body.pageNum}}/{{Body.pages}}</button>
        <button type="button" class="btn btn-default" @click='nextPage'>下一页</button>
        <button type="button" class="btn btn-default" @click='lastPage'>末页</button>
      </div>
      <div class="btn-group pull-right" role="group" aria-label="...">
        <button type="button" class="btn btn-default">每页10条</button>
        <button type="button" class="btn btn-default">共{{Body.pages}}页</button>
      </div>
    </div>
  </div>
</template>
<script>  
export default {
  data() {
    return {}
  },
  methods: {
    homePage() {
      this.Condition.pageNumber = 1
      this.$emit('customEvent', this.Condition)
    },
    lastPage() {
      this.Condition.pageNumber = this.Body.pages
      this.$emit('customEvent', this.Condition)
    },
    previousPage() {
      if (this.Condition.pageNumber <= 1) {
        this.$message({
          message: '已经是第一页',
          type: 'warning'
        })
        return
      }
      this.Condition.pageNumber--
      this.$emit('customEvent', this.Condition)
    },
    nextPage() {
      if (this.Condition.pageNumber >= this.Body.pages) {
        this.$message({
          message: '已经是最后一页',
          type: 'warning'
        })
        return
      }
      this.Condition.pageNumber++
      this.$emit('customEvent', this.Condition)
    },
  },
  props: ['Body', 'Condition']
}
</script>
<style scoped>
.flip .flipBtn {
  text-align: center;
}
</style>


