</style>
<script>
import { mapActions, mapGetters, mapState, mapMutations } from 'vuex'
//头部
import CommonHeader from '../public/header.vue';
//tab导航
import Set from '../public/set.vue';
import $ from 'jquery';
import moment from 'moment';
// import AddReservation from '../../components/experts/component/addReservation.vue'
import Public from '../../untils/Public.js'
import flip from '../public/flip.vue'
export default {
  name: 'PatientDetails',
  mounted() {
    var that = this
    this.$nextTick(function() {
      $('.importantLong .el-form-item__label').css({
        width: '120px',
        'margin-left': '-20px'
      })

    })
  },
  data() {
    return {
      span1: true,
      span2: false,
      span3: false,
      patientsMeans: true,
      patientsExit: false,
      newCaseShow: false,
      Authorization: '',
      patientMes: {},
      patient: {},
      editable: false,
      recordList: [],
      AddReservationShow: false,//控制新增病历的显示隐藏
      recordDetailShow: true,//控制病历详情的显示隐藏
      ReType: [],
      addRecordList: {
        "age": "",
        "allergyHistory": "",
        "auxiliaryCheck": "",
        "caseNo": "",
        "chiefComplaint": "",
        "clinicsId": "",
        "createdBy": "",
        "createdWhen": "",
        "deleteFlag": "",
        "diagnosisOpinion": "",
        "doctorId": "",
        "expertId": "",
        "familyHistory": "",
        "history": "",
        "id": 0,
        "infectionHistory": "",
        "orderId": "",
        "pastMedicalHistory": "",
        "patientId": "",
        "personalHistory": "",
        "recordCheck": "",
        "treatmentSuggestion": "",
        "updatedBy": "",
        "updatedWhen": "",
        "attachments": []
      },
      consultationRecordList: [],
      noMesShow3: false,
      routerId: '',
      rules: {
        chineseName: [
          { required: true, message: '请输入姓名', trigger: 'blur' },
          { validator: (rule, value, callback) => { if (/^([\u4e00-\u9fa5]{1,20}|[a-zA-Z\.\s]{1,20})$/.test(value) == false) { callback(new Error("请填写正确姓名")); } else { callback(); } }, trigger: 'blur' }
        ],
        euMobile: [
          { required: true, message: '请填写手机', trigger: 'blur' },
          { validator: (rule, value, callback) => { if (/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/.test(value) == false) { callback(new Error("请填写正确手机")); } else { callback(); } }, trigger: 'blur' }
        ],
        sex: { required: true, message: '请选择性别' },
        birthday: { type: 'date', required: true, message: '请输入出生日期', trigger: 'change' },
        emergencyContact: [
          { required: true, message: '请输入紧急联系人', trigger: 'blur' },
          { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' }
        ],
        emergencyMobile: [
          { required: true, message: '请填写手机', trigger: 'blur' },
          { validator: (rule, value, callback) => { if (/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/.test(value) == false) { callback(new Error("请填写正确手机")); } else { callback(); } }, trigger: 'blur' }
        ],
        chiefComplaint: { required: true, message: '请填写主诉', tigger: 'blur' },
        history: { required: true, message: '请填写现病史', tigger: 'blur' },
        address: { min: 0, max: 30, message: '长度最多为30个字符', trigger: 'blur' }
      },
      age: '',
      imageArr: [],//影像数组
      imageShow: true,
      arrowdiRectionArr1: [],
      xieZhenParams: {
        userId: '',
        pageNumber: 1,
        pageSize: 10
      },
      totalAll: 0,
      consultationBody: {},
      isBlowUp: false,//放大
      blowUpImgUrl: '',
      scrollY: 0,//保留浏览器位置
      progressStyle: {
        position: 'absolute',
        left: '50%',
        top: '50%',
        marginLeft: '-50px',
        marginTop: '-50px'
      },//进度条样式
      percentage: [],//进度条进度
      progressIsShow: [],//进度条是否显示
      pickerOptions0: {
        disabledDate: function(time) {
          return time.getTime() > Date.now();
        },
      }
    }
  },
  created() {
    // 患者详情
    this.patientDetails();
    // 疾病类型
  },
  // computed:{
  //   ...mapState(['test']),
  //   ...mapGetters(['testFilter'])
  // },
  methods: {
    // ...mapActions({
    //   add:'CHANGTEST'
    // }),
    // 提示框
    success() {
      this.$message({
        message: '保存成功',
        type: 'success'
      });
    },
    warning() {
      this.$message({
        message: '保存失败',
        type: 'warning'
      });
    },
    success2() {
      this.$message({
        message: '删除成功',
        type: 'success'
      });
    },
    warning3() {
      this.$message({
        message: '不是当天的病历不可编辑',
        type: 'warning'
      });
    },
    Arrwarning(mes) {
      this.$message({
        message: mes,
        type: 'warning'
      });
    },
    imageUploadWarning() {
      this.$message({
        message: '图片上传失败',
        type: 'warning'
      });
    },
    imageShowBtn(index) {
      this.imageShow = true;
      this.arrowdiRectionArr1[index] = !this.arrowdiRectionArr1[index]
      for (var i = 0, len = this.arrowdiRectionArr1.length; i < len; i++) {
        if (i != index && this.arrowdiRectionArr1[i] == false) {
          this.arrowdiRectionArr1[i] = true
        }
      }
      this.arrowdiRectionArr1 = this.arrowdiRectionArr1.concat()
    },
    tapClass(index) {
      $('.p-b-nav li').eq(index).addClass('active')
      $('.p-b-nav li').eq(index).siblings().removeClass('active')
    },
    //  患者编辑按钮
    pExit() {
      this.patientsMeans = false;
      this.patientsExit = true;
    },
    // 患者资料的保存
    keepFn() {
      var that = this
      this.$refs['patient'].validate((valid) => {
        if (valid) {
          that.patientsMeans = true;
          that.patientsExit = false;
          var url = '/api/patient/patients';
          that.patient = JSON.stringify(that.patient)
          that.$http.post(url, that.patient, {
            headers: {
              Authorization: 'bearer ' + that.Authorization
            },
            emulateJSON: true
          }).then(function() {
            this.patientDetails();
            this.success()
          }, function() {
            this.warning();
          })
        } else {
          return false;
        }
      });

    },
    // 患者资料的取消
    cancelFn() {
      this.$refs['addRecordList'].validate()
      this.patientsMeans = true;
      this.patientsExit = false;
      this.patientDetails();
    },
    // 路由到请求协诊
    requestXz() {
      this.$router.push({ name: 'Expert', query: { id: this.$route.params.id } })
    },
    // 返回患者页面
    backPatient() {
      this.$router.push({ name: 'Patients' })
    },
    // 路由到订单详情
    OrderDetails(id, recordID) {
      this.$router.push({ name: 'OrderDetails', query: { id: id, recordId: recordID } })
    },
    // 新增按钮的显示隐藏
    newCaseShow1() {
      this.newCaseShow = false;
      // 回到资料显示模块
      this.patientsMeans = true;
      this.patientsExit = false;
    },
    newCaseShow2() {
      this.newCaseShow = true;
      // 回到资料显示部分
      this.AddReservationShow = false;
      this.recordDetailShow = true;
    },
    newCaseShow3() {
      this.newCaseShow = false;
    },
    // 患者的详细信息
    patientDetails() {
      this.Authorization = localStorage.getItem('access_token');
      var url = '/api/patient/patients/' + this.$route.params.id;
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + this.Authorization
        }
      }).then(function(res) {
        this.patientMes = res.body
        if (res.body.firstVisitDate) { res.body.firstVisitDate = moment(res.body.firstVisitDate).format('YYYY-MM-DD') }
        if (res.body.returnVisitDate) { res.body.returnVisitDate = moment(res.body.returnVisitDate).format('YYYY-MM-DD') }
        this.patient = {
          "account": res.body.account,
          "address": res.body.address,
          "birthday": (new Date(res.body.birthday)),
          "caseNo": res.body.caseNo,
          "chineseName": res.body.chineseName,
          "description": res.body.description,
          "emergencyContact": res.body.emergencyContact,
          "emergencyMobile": res.body.emergencyMobile,
          "euEmail": res.body.euEmail,
          "euMessageSet": res.body.euMessageSet,
          "euMobile": res.body.euMobile,
          "euNickname": res.body.euNickname,
          "euNumber": res.body.euNumber,
          "firstVisitDate": res.body.firstVisitDate,
          "firstVisitDoc": res.body.firstVisitDoc,
          "hospitalId": res.body.hospitalId,
          "id": res.body.id,
          "idNumber": res.body.idNumber,
          "imageUrl": res.body.imageUrl,
          "loginType": res.body.loginType,
          "recordType": res.body.recordType,
          "registerPlatId": res.body.registerPlatId,
          "registerType": res.body.registerType,
          "returnVisitDate": res.body.returnVisitDate,
          "returnVisitDoc": res.body.returnVisitDoc,
          "sex": res.body.sex,
          "userType": res.body.userType,
          "usersId": res.body.usersId
        }
        // 日期旁边的年龄显示
        this.age = Math.ceil((new Date() - this.patient.birthday) / 1000 / 60 / 60 / 24 / 365)
        // 电子病历
        this.medicalRecord();
        // 协诊记录
        this.consultationRecord();
      })
    },
    // 获取电子病历
    medicalRecord() {
      var url = '/api/patient/records';
      var usersId = this.patient.usersId
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + this.Authorization
        },
        params: {
          userId: usersId
        }
      }).then(function(res) {
        //  this.recordList=JSON.parse(res.bodyText).list;
        this.recordList = res.body
        for (var i = 0; i < res.body.length; i++) {
          if (i == 0) {
            this.arrowdiRectionArr1.push(false)
            continue;
          }
          this.arrowdiRectionArr1.push(true)
        }
      })
    },
    // 新增病历
    addKeepFn() {
      var that = this
      this.$refs['addRecordList'].validate((valid) => {
        if (valid) {
          var url = '/api/patient/records';
          that.addRecordList.patientId = that.patient.usersId
          that.addRecordList.age = that.age;
          that.addRecordList.attachments = that.imageArr || []
          var addRecordListString = JSON.stringify(that.addRecordList);
          that.$http.post(url, addRecordListString, {
            headers: {
              Authorization: 'bearer ' + that.Authorization
            }
          }).then(function(res) {
            this.success()
            this.medicalRecord();
            this.AddReservationShow = false;
            this.recordDetailShow = true;
          }, function(res) {
            this.$message({
              message: res.body.message,
              type: 'warning'
            });
          })
        } else {
          window.scrollTo(0, 0)//解决小屏幕用户看不到的问题            
          return false;
        }
      });
    },
    // 取消新增或者编辑的按钮
    addCancelFn() {
      this.medicalRecord()//预防修改没成功。用户又取消按钮，肉眼看以为修改成功了
      this.$refs['addRecordList'].validate();
      this.AddReservationShow = false;
      this.recordDetailShow = true;
    },
    // 新增病历的显示隐藏
    exitShow() {
      this.imageArr = []
      this.percentage = [];//进度条进度
      this.progressIsShow = []//进度条是否显示
      this.addRecordList = {
        "age": "",
        "allergyHistory": "",
        "auxiliaryCheck": "",
        "caseNo": "",
        "chiefComplaint": "",
        "clinicsId": "",
        "createdBy": "",
        "createdWhen": "",
        "deleteFlag": "",
        "diagnosisOpinion": "",
        "doctorId": "",
        "expertId": "",
        "familyHistory": "",
        "history": "",
        "id": 0,
        "infectionHistory": "",
        "orderId": "",
        "pastMedicalHistory": "",
        "patientId": "",
        "personalHistory": "",
        "recordCheck": "",
        "treatmentSuggestion": "",
        "updatedBy": "",
        "updatedWhen": "",
        "attachments": []
      }
      this.AddReservationShow = true;
      this.recordDetailShow = false;
      this.$refs['addRecordList'].resetFields()
    },
    // 编辑病历保存
    exitRecord(id) {
      this.imageShow = true;
      this.AddReservationShow = true;
      this.recordDetailShow = false;
      for (var i = 0; i < this.recordList.length; i++) {
        if (id == this.recordList[i].id) {
          this.addRecordList = this.recordList[i]
          // 进度条
          var attachments = this.addRecordList.attachments || []
          this.percentage = []
          this.progressIsShow = []
          for (var j = 0; j < attachments.length; j++) {
            this.percentage.push(25)
            this.progressIsShow.push(false)
          }
          // 进度条          
          this.imageArr = this.addRecordList.attachments
          if (!this.imageArr) {
            this.imageArr = []
          }

        }
      }
    },
    // 删除指定的病历
    deleteRecord(id) {
      var url = '/api/patient/records/' + id;
      this.$http.delete(url, {
        headers: {
          Authorization: 'bearer ' + this.Authorization
        }
      }).then(function() {
        this.success2()
        this.medicalRecord()
      }, function(res) {
        var that = this
        function warningErr() {
          that.$message({
            message: res.body.message,
            type: 'warning'
          });
        }
        warningErr()
      })
    },
    // 获取协诊记录
    consultationRecord() {
      var url = '/api/assist-order/assistOrders/history'
      this.xieZhenParams.userId = this.patient.usersId

      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + this.Authorization
        },
        params: this.xieZhenParams
      }).then(function(res) {
        this.totalAll = res.body.lastPage
        this.consultationRecordList = res.body.list;
        if (this.consultationRecordList.length == 0) {
          this.noMesShow3 = true;
        }
        this.consultationBody = res.body
      })
    },
    // 头像上传  未成功
    uploadImage() {
      // 限制张数
      if (this.imageArr.length >= 20) {
        this.Arrwarning('最多上传20张')
        return
      }
      var that = this;
      this.Authorization = localStorage.getItem('access_token');
      Public.publicLo.senImg('fileByReservation', function(e) {
        var url = '/api/basic/employees/upload'
        that.imageArr.push({
          "createdBy": '',
          "createdWhen": '',
          "deleteFlag": '',
          "fromId": '',
          "fromType": '',
          "id": '',
          "name": '',
          "path": e.target.result,
          "type": '',
          "updatedBy": '',
          "updatedWhen": ''
        })
        that.percentage.push(25)
        that.progressIsShow.push(true)
        var image = { imageUrl: e.target.result }
        image = JSON.stringify(image);
        that.$http.post(url, image, {
          headers: {
            Authorization: 'bearer ' + that.Authorization
          },
          emulateJSON: true
        }).then(function(res) {
          // 设置
          that.$set(that.imageArr[that.imageArr.length - 1], 'path', res.body.path[0])
          that.$set(that.percentage, that.percentage.length - 1, 100)
          var timer = setTimeout(function() {
            // 设置            
            that.$set(that.percentage, that.percentage.length - 1, 25)
            that.$set(that.progressIsShow, that.progressIsShow.length - 1, false)
          }, 1000)
          document.getElementById('fileByReservation').value = ''
        }, err => {
          that.percentage.splice(that.percentage.length - 1, 1)
          that.progressIsShow.splice(that.progressIsShow.length - 1, 1)
        })
      })
    },
    // 删除指定的影像
    deleteImageUrl(index) {
      document.getElementById('fileByReservation').value = ''
      this.imageArr.splice(index, 1);
      this.percentage.splice(index, 1);
      this.progressIsShow.splice(index, 1)
    },
    blowUp(url) {
      // window.open(url.path)
      // 同一个页面的做法
      this.scrollY = window.scrollY
      this.isBlowUp = true;
      this.blowUpImgUrl = url.path;
    },
    wheel(event) {
      event.preventDefault()
    },
    isBlowUpShow() {
      this.isBlowUp = false;
      var that = this;
      setTimeout(function() {
        window.scrollTo(0, that.scrollY)
      }, 30)
    },
    customEvent(xieZhenParams) {
      this.xieZhenParams = xieZhenParams
      this.consultationRecord()
    }
  },
  components: {
    CommonHeader: CommonHeader,
    Set: Set,
    flip: flip
    // AddReservation:AddReservation
  }

}

</script>



